import * as moment from 'moment';

export enum NotificationType {
  Message = 'MESSAGE',
  Task = 'TASK',
}

export enum MessageSource {
  /**  */
  ALL = 'ALL',
  /**  */
  AUTH = 'AUTH',
  /**  */
  BPMS = 'BPMS',
  /**  */
  DATA_ASSETS = 'DATA_ASSETS',
}

interface MessageProfile {
  /**  */
  content?: string;

  /** HIGH/MEDIUM/LOW */
  messageLevel?: string;

  /** : DQE, VOLEMORT, OTHERS */
  messageSource?: string;

  /** SYSTEM/CUSTOMIZE */
  messageType?: string;

  /** Url */
  messageUrl?: string;

  /**  */
  title?: string;
}

export interface StationLetterDTO {
  /**  */
  haveRead?: boolean;

  /**  */
  messageProfile?: MessageProfile;

  /**  */
  sendTime?: string;

  /** Id */
  stationLetterId?: number;

  /** Id */
  userId?: string;
}

export interface ToBeHandleOaTaskBO {
  /** bizContent */
  bizContent?: string;

  /** createTime */
  createTime?: string;

  /** createUserId */
  createUserId?: string;

  /** createUserName */
  createUserName?: string;

  /** finishTime */
  finishTime?: string;

  /** name */
  name?: string;

  /** objectId */
  objectId?: number;

  /** objectType */
  objectType?: 'APPLY' | 'APPROVE';

  /** sourceSystem */
  sourceSystem?: string;

  /** status */
  status?: 'APPROVING' | 'ACCEPTED' | 'REJECTED' | 'REVOKED';

  /** type */
  type?: 'APPROVE' | 'MANAGE' | 'OTHERS';
}

export type Notification = StationLetterDTO | ToBeHandleOaTaskBO;

export interface NormalizedNotification {
  id: number;
  icon: string;
  sourceName: string;
  title: string;
  time: moment.Moment;
  url: string;
}
